@import url(https://fonts.googleapis.com/css2?family=Actor&family=Syncopate:wght@700&display=swap);
.card-list {
        /* max-width: 700px; */
        width: 85vw;
        margin: 30px auto;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-gap: 20px;
}
.card-container {
        display: flex;
        flex-direction: column;
        background-color: #95dada;
        border: 1px solid grey;
        border-radius: 5px;
        padding: 25px;
        cursor: pointer;
        -moz-osx-font-smoothing: grayscale;
        -webkit-backface-visibility: hidden;
                backface-visibility: hidden;
        transform: translateZ(0);
        transition: transform 0.25s ease-out;
        text-align: center;
        font-family: 'Actor',
                sans-serif;
        border: none;

}

.card-container:hover {
        transform: scale(1.05);
}
body {
        background: linear-gradient(90deg, hsla(141, 54%, 86%, 1) 0%, hsla(333, 73%, 85%, 1) 50%, hsla(211, 58%, 79%, 1) 100%);
}

.App {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

}

h1 {
        font-family: 'Syncopate',
                sans-serif;
}
input {
        padding: 10px;
        border: rgb(104, 104, 104) 2px solid;
        border-radius: 5px;
        outline: none;
        font-size: 20px;
        font-family: 'Actor',
                sans-serif;
        width: 400px;
}
